import authApi from "../authApi"
import { FormDataConfig } from "../config"

export const getBusList = async (dojangId) => {
  const res = await authApi.get(`car/carread/${dojangId}/0`)
  return res.data
}

export const getBusInfo = async (dojangId, busId) => {
  const res = await authApi.get(`car/carread/${dojangId}/${busId}`)
  return res.data
}

export const updateBusInfo = async (dojangId, busId, body) => {
  const res = await authApi.post(`car/carupdate/${dojangId}/${busId}`, body, FormDataConfig)
  return res
}

export const deleteBus = async (dojangId, busId) => {
  const res = await authApi.delete(`car/cardelete/${dojangId}/${busId}`)
  return res
}

export const createBus = async (body) => {
  const res = await authApi.post(`car/carcreate`, body, FormDataConfig)
  return res
}

