import React from 'react'
import PageContainer from '../../../../components/layout/PageContainer'
import BusList from './BusList/BusList'
import CreateBtnMD from '../../../../components/UI/Atoms/Button/CreateBtnMD'
import ModalContainer from '../../../../components/layout/ModalContainer'
import { useModal } from '../../../../hooks/basic/useModal'
import ResistBus from './ResistBus'

const ManageBus = () => {
  
  // custom hook
  const [open, openFn, closeFn] = useModal()

  return (
    <>
    <PageContainer width='medium'>
      <div className='mb-[10px] ml-auto'>
        <CreateBtnMD onClick={openFn}>+ 차량 등록</CreateBtnMD>
      </div>
      <BusList />
    </PageContainer>

    <ModalContainer
      open={open}
      onClose={closeFn}
      title='차량 등록'
    >
      <ResistBus />
    </ModalContainer>
    </>
  )
}

export default ManageBus