import React, { useMemo } from 'react'
import { styled } from '@mui/system';
import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box'
import BackBtnSM from '../UI/Atoms/Button/BackBtn';

const DocStyle = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 800,
  height: 895,
  bgcolor: '#ffffff',
  overflow:'auto',
};



const ModalContainer = ({ width, height, doc, open, onClose, title, children, }) => {

  const BoxStyle = useMemo(() => {
    return {
      position: 'absolute',
      top: '50%',
      left: '50%',
      transform: 'translate(-50%, -50%)',
      width: width || 900,
      height: height || 800,
      bgcolor: '#F2F2F2',
      borderRadius: '10px',
      boxShadow: 24,
      pt: '30px',
      px: '30px',
      pb: '30px',
      overflow:'auto',
    }
  }, [])

  return (
    <>
    <Modal
      open={open}
      onClose={onClose}
    >
      <Box sx={doc ? DocStyle : BoxStyle}>
        { !doc &&
          <div className='flex items-center border-solid border-b border-x-0 border-t-0 border-[#aaaaaa] pb-[15px]'>
            <BackBtnSM onClick={onClose}></BackBtnSM>
            <div className='text-[24px] font-bold'>
              {title}
            </div>
          </div>
        }

        {children}
      </Box>
    </Modal>
    </>
  )
}

export default ModalContainer