import { Box, TextField } from '@mui/material'
import React from 'react'
import { Label } from '../../../../pages/UserDojang/Manage/Student/component/StudentInfo/StudentForm/StudentForm'
import BottomDescription from './BottomDescription'
import BoxPlacholder from './BoxPlacholder'

const TextBox = ({ title, edit, ...props }) => {

  return (
    <div className="mt-[30px]">
      <Label>{title}</Label>
      <div className="h-[100px] mt-[10px] mb-[4px] px-[10px] py-[10px] border border-solid border-[1px] border-[#aaaaaa]">
          <textarea 
            className="w-[100%] h-[100%] border-none resize-none outline-none disabled:bg-[#fff] text-[#000000]" 
            rows='4'
            disabled={edit}
            {...props} 
            />
      </div>
    </div>
  )
}

export default TextBox