import React, {useRef, useState} from 'react'
import uuid from 'react-uuid'

export const useFiles = ({ data, urlKey, fileKey, set, fileLength }) => {
  const fileInputRef = useRef()

  const clickUpload = () => {
    fileInputRef.current.click()
  }
  
  const handleFileInput = (e) => {
    const urlList = data[urlKey]
    const fileList = e.target.files
    if ((urlList?.length + fileList?.length) > fileLength) {
      alert(`${fileLength}개 까지만 등록할 수 있습니다.`)
    } else {
      const temp = { ...data, [fileKey]:[] }
      for (let i = 0; i < fileList.length; i++) {
        const currentFile = fileList[i]
        const currentUrl = URL.createObjectURL(fileList[i])

        temp[fileKey].push(currentFile)
        temp[urlKey].push(currentUrl)
      }

      set(temp)
    }
  }

  return [ handleFileInput, fileInputRef, clickUpload ]
}
