import { useMutation } from '@tanstack/react-query'
import React, { useState } from 'react'
import { useRecoilValue } from 'recoil'
import { deleteBus, updateBusInfo } from '../../../../../../api/Bus/BusApi'
import AlertText from '../../../../../../components/common/Alert/AlertText'
import Box from '../../../../../../components/layout/Box'
import ModalContainerAlert from '../../../../../../components/layout/ModalContainerAlert'
import { useDebug } from '../../../../../../hooks/basic/useDebug'
import { useModal } from '../../../../../../hooks/basic/useModal'
import { useInputs } from '../../../../../../hooks/input/useInputs'
import dojangIdAtom from '../../../../../../recoil/atom/dojangIdAtom'
import { getDescription } from '../../../../../../utils/render/getDescription'
import { Col, Input, Label, Row } from '../../../Student/component/StudentInfo/StudentForm/StudentForm'
import ImgBox from '../../../../../../components/common/Form/BoxForm/ImgBox'
import TextBox from '../../../../../../components/common/Form/BoxForm/TextBox'
import CreateBtnSM from '../../../../../../components/UI/Atoms/Button/CreateBtnSM'
import BlackBtnSM from '../../../../../../components/UI/Atoms/Button/BlackBtnSM'
import WhiteBtnSM from '../../../../../../components/UI/Atoms/Button/WhiteBtnSM'
import { useFiles } from '../../../../../../hooks/file/useFiles'
import { getFormData } from '../../../../../../utils/basic/getFormData'
import { formDataDebug } from '../../../../../../utils/debug/debug'

const BusInfo = ({ info }) => {
  const dojangId = useRecoilValue(dojangIdAtom)
  const busId = info?.id
  const [ edit, setEdit ] = useState(false)

  const defaultValue = {
    ...info, 
    delImgname:[], 
    img:[]
  }

  // custom hook
  const [ open, openFn, closeFn ] = useModal()
  const [ editData, setEditdata, handleChange ] = useInputs(defaultValue)

  const [ handleImgInput, fileInputRef, clickUpload ] = useFiles({
    data: editData,
    set: setEditdata,
    urlKey: 'img_url',
    fileKey: 'img',
    fileLength: 3,
  })
  
  // query
  const { mutate: saveMutate, isLoading: saveLoading } = useMutation((body) => updateBusInfo(dojangId, busId, body))
  const { mutate: deleteMutate, isLoading: deleteLoading } = useMutation(() => deleteBus(dojangId, busId))

  // handler
  const handleEdit = () => {
    setEdit(true)
  }

  const handleSave = () => {
    const { id, createdAt, updatedAt, dojang, img_url, delImgname, img, ...postData} = editData
    const delList = []
    if (delImgname != []) {
      for (let i=0; i < delImgname.length; i++) {
        if (!delImgname[i].includes('blob:')) {
          let temp = delImgname[i].split('com/')[1]
          delList.push(temp)
        }
      }
    }
    const data = []
    if (delList != []) {
      data.push(...Object.entries({ ...postData, delImgname: JSON.stringify(delList) }))
    } else {
      data.push(...Object.entries(postData))
    }

    if (img != []) {
      for (const v of img){
        data.push(['img', v])
      }
    }

    const formData = getFormData(data)
    // formDataDebug(formData)
    saveMutate(formData, {
      onSuccess: () => window.location.reload()
    })
  }

  const handleCancle = () => {
    setEdit(false)
    setEditdata({...defaultValue})
  }

  const handleDel = () => {
    deleteMutate(busId, {
      onSuccess: () => window.location.reload()
    })
  }

  // handler -img
  const handleDeleteImg = (e) => {
    let idx  = e.currentTarget.id
    let temp = editData.img_url.filter((v,i) => i!=idx)
    let tempDel = editData.img_url.filter((v,i) => i==idx)
    setEditdata({
      ...editData, 
      img_url: temp, 
      delImgname: [...editData.delImgname, tempDel[0]]
    })
  }

  // useDebug(editData)

  // useDebug(info)


  return (
    <>
      <Box className='mt-[22px]'
        title='차량 정보'
        btn={
          <div className='flex gap-2'>
            { edit ? 
            <>
              <WhiteBtnSM onClick={handleCancle}>취소</WhiteBtnSM>
              <BlackBtnSM onClick={handleSave} disabled={saveLoading}>저장</BlackBtnSM>
            </>
            :
            <>
              <BlackBtnSM onClick={handleEdit}>수정</BlackBtnSM>
              <WhiteBtnSM onClick={openFn} disabled={deleteLoading}>삭제</WhiteBtnSM>
            </>
            }
          </div>
        }
      >
        <Row>
          <Col col={1}>
            <Label>차량별칭</Label>
            <Input 
              name="name"
              value={editData?.name || ''}
              onChange={handleChange}
              disabled={!edit}
            />
          </Col>
          <Col col={1}>
            <Label>차량번호</Label>
            <Input 
              name="number"
              value={editData?.number || ''}
              onChange={handleChange}
              disabled={!edit}
            />
          </Col>
        </Row>
        <Row>
          <Col col={1}>
            <Label>차종</Label>
            <Input 
              name="type"
              value={editData?.type || ''}
              onChange={handleChange}
              disabled={!edit}
            />
          </Col>
          <Col col={1}>
            <Label>인승</Label>
            <Input 
              name="seater"
              value={editData?.seater || ''}
              onChange={handleChange}
              disabled={!edit}
            />
          </Col>
        </Row>
        <ImgBox
          title={`차량 사진  (${editData?.img_url?.length || '0'}/3)`}
          imgs={editData?.img_url}
          placeholder='차량 사진을 추가해 주세요'
          description={getDescription('img')}
          onDeleteImg={handleDeleteImg}
          edit={!edit}
          addBtn={
            edit && <CreateBtnSM onClick={clickUpload} className='w-[100px] h-[30px] text-[14px]'>+ 사진 추가</CreateBtnSM>
          }
        />
        <input ref={fileInputRef} type='file' accept='image/*' className='hidden' multiple onChange={handleImgInput}/>
        <TextBox
          title='차량 참고사항'
          placeholder='참고가 될 차량 정보를 적어 주세요.'
          name='memo'
          value={editData?.memo}
          onChange={handleChange}
          edit={!edit}
        />
      </Box>
      

      <ModalContainerAlert
        open={open}
        onClose={closeFn}
        onConfirm={handleDel}
        confirmText='삭제'
      >
        <AlertText title>
          차량을 삭제 하시겠습니까?
        </AlertText>
        <AlertText>
          삭제하시면 복구가 불가능 합니다.
        </AlertText>
      </ModalContainerAlert>
    </>
  )
}

export default BusInfo