
export function toUrlList(v) {
  const _list = v ? v.replaceAll('[','').replaceAll(']','').replaceAll('\"','').split(',') : ''
  const imgBaseURL = 'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/'
  if (_list) {
    for (let i=0 ; i<_list.length ; i++) {
      _list[i] = imgBaseURL + _list[i]
    }
  }
  return _list
}

export function toLevelString(v) {
  switch (v) {
    case 1 : return '1급'
    case 2 : return '2급'
    case 3 : return '3급'
    case 4 : return '4급'
    case 5 : return '5급'
    case 6 : return '6급'
    case 7 : return '7급'
    case 8 : return '8급'
    case 9 : return '9급'
    case 101 : return '1품'
    case 102 : return '2품'
    case 103 : return '3품'
    case 104 : return '4품'
    case 1001 : return '1단'
    case 1002 : return '2단'
    case 1003 : return '3단'
    case 1004 : return '4단'
    case 1005 : return '5단'
    case 1006 : return '6단'
    case 1007 : return '7단'
    case 1008 : return '8단'
    case 1009 : return '9단'
    default: return ''
  }
}