import React from 'react'
import { Label } from '../../../../pages/UserDojang/Manage/Student/component/StudentInfo/StudentForm/StudentForm'
import { imgBaseURL } from '../../../../utils/basic/URL'
import CancleIconBtn from '../../../UI/Atoms/Button/CancleIconBtn'
import BottomDescription from './BottomDescription'
import BoxPlacholder from './BoxPlacholder'

const ImgBox = ({ title, imgs, onDeleteImg, edit, placeholder, description, addBtn }) => {



  return (
    <div className="mt-[30px]">
      <div className="flex justify-between">
        <Label>{title}</Label>
        {addBtn}

      </div>
      <div className="h-[100px] flex gap-[15px] mt-[10px] mb-[4px] px-[10px] py-[10px] border border-solid border-[1px] border-[#aaaaaa]">
        { imgs[0] ?
          React.Children.toArray(
            imgs?.map( (imgUrl, idx) =>
              <div className='relative h-[70px] my-[5px]'>
                <img className="w-[70px] h-[70px] border border-solid border-[1px] border-[#dddddd] rounded-[2px]" src={imgUrl} />
                { !edit && 
                  <CancleIconBtn 
                    className="absolute top-[-5px] right-[-5px] w-[20px] h-[20px] bg-[#fff] hover:bg-[#dddddd]" 
                    id={idx}
                    onClick={onDeleteImg} 
                  />
                }
              </div>
            )
          )
          :
          <BoxPlacholder>
            { placeholder }
          </BoxPlacholder>
        }
      </div>
        <BottomDescription>
          { !edit ? description : '　' }
        </BottomDescription>
    </div>
  )
}

export default ImgBox