import { useMutation, useQuery } from '@tanstack/react-query'
import React, { useState } from 'react'
import { useRecoilValue } from 'recoil'
import { getBusInfo, getBusList } from '../../../../../api/Bus/BusApi'
import { Grid } from '../../../../../components/common/Grid/Grid'
import dojangIdAtom from '../../../../../recoil/atom/dojangIdAtom'
import DirectionsBusFilledOutlinedIcon from '@mui/icons-material/DirectionsBusFilledOutlined';
import Description from '../../../../../components/UI/Atoms/Ex/Description'
import Em from '../../../../../components/UI/Atoms/Ex/Em'
import ArrowIcon from '../../../../../components/UI/Atoms/Icon/ArrowIcon'
import { useDebug } from '../../../../../hooks/basic/useDebug'
import { useModal } from '../../../../../hooks/basic/useModal'
import ModalContainer from '../../../../../components/layout/ModalContainer'
import BusInfo from './BusInfo/BusInfo'
import { toUrlList } from '../../../../../utils/basic/transform'

const BusList = () => {
  const dojangId = useRecoilValue(dojangIdAtom)
  const [ busInfo, setBusInfo ] = useState('')

  // custom hook
  const [ open, openFn, closeFn ] = useModal()

  // query
  const bus = useQuery(['getBusList', dojangId], () => getBusList(dojangId), {
    enabled: !!dojangId,
  })

  const { mutate : busMutate } = useMutation((busId) => getBusInfo(dojangId, busId))
  const getInfo = (busId) => {
    busMutate(busId, {
      onSuccess: (res) => {
        console.log(res)
        let temp = {
          ...res, 
          img_url: toUrlList(res.img_url)
        }
        setBusInfo(temp)
        openFn()
      }
    })
  }

  // handler
  const handleClickItem = (e) => {
    const clickId = e.currentTarget.id
    getInfo(clickId)
  }


  return (
    <>
    <Grid>
      <Grid.Head className='pl-[20px]'>
        <Description>현재 등록된 차량은 <Em>{bus?.data?.count || '0'}대</Em> 입니다.</Description>
      </Grid.Head>
      <Grid.RowWrap>
        { bus?.data?.rows &&
          React.Children.toArray(
            bus?.data?.rows?.map( item => 
              <Grid.Row
                id={item?.id}
                onClick={handleClickItem}
              >
                <Grid.Col w={1} className='flex-none'>
                  <div className='bg-[#f5f7f8] w-[30px] h-[30px] rounded-full p-[5px]'>
                    <DirectionsBusFilledOutlinedIcon className='w-[20px] h-[20px] flex text-[#333333] m-auto' />
                  </div>
                </Grid.Col>
                <Grid.Col w={3}>{item?.name}</Grid.Col>
                <Grid.Col w={3}>{item?.number}</Grid.Col>
                <Grid.Col w={4}>{item?.type} {item?.seater}</Grid.Col>
                <Grid.Col w={1} className='pr-0 text-center'><ArrowIcon /></Grid.Col>
              </Grid.Row>  
            )
        )}
    </Grid.RowWrap>
  </Grid>

  <ModalContainer
    open={open}
    onClose={closeFn}
    title='차량 정보'
  >
    <BusInfo info={busInfo}/>
  </ModalContainer>
  </>
  )
}

export default BusList